* {
  font-family: "Ubuntu Mono", monospace;
  color: #2c2c2c;
}

h1 {
  font-size: 50px;
}

p {
  color: #e3e3e2;
}

span.red {
  color: #d30202;
}

span.green {
  color: #005e00;
}

span.black {
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}

span.orange {
  color: #a35d00;
}

.bg {
  margin: 0;
  background: #009CE1;
  width: 100%;
  height: 100%;
}

#sticky-header {
  display: flex;
  justify-content: space-between;
  background: rgba(137, 207, 240, 0.9450980392);
  position: fixed;
  width: 100%;
  height: 60px;
  top: 0;
  background-image: linear-gradient(to bottom, rgba(137, 207, 240, 0.95), transparent);
  -webkit-mask-image: linear-gradient(to bottom, white, transparent);
  mask-image: linear-gradient(to bottom, white, transparent);
}
#sticky-header #mobile-nav-menu,
#sticky-header #home {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  width: 60px;
}
#sticky-header i {
  font-size: 35px;
  opacity: 0.8;
}

#greeting {
  display: flex;
  flex-direction: column;
  margin-top: 60px;
  justify-content: center;
  text-align: center;
}
#greeting p {
  margin-top: 40px;
}

#empty-div {
  width: 100%;
  height: 400px;
}

#about-me {
  margin: 0 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
#about-me p {
  text-align: start;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.arm {
  animation: wave 2s infinite;
  transform-origin: 100px 90px;
  /* Set the origin point for the arm rotation */
}

@keyframes wave {
  0% {
    transform: rotate(60deg);
  }
  50% {
    transform: rotate(90deg);
  }
  100% {
    transform: rotate(60deg);
  }
}